<template>
  <div class="page">
    <div class="container">
      <div class="ringtoss">
        <mk-ringtoss
          ref="ringtoss"
          :prizeImgArray="prizeImgArray"
          @stateChange="stateChange"
          @clickStart="clickStart"
        />
        <div class="info">
          <p>当前状态：{{ state }}</p>
        </div>
      </div>
    </div>
    <demo-block card title="基础操作">
      <button
        class="common-button primary"
        :disabled="!canStart"
        @click="clickStart"
      >
        开始
      </button>
      <button class="common-button primary" @click="reset">重置</button>
      <button class="common-button primary" @click="disable">禁用</button>
      <button class="common-button primary" :disabled="!canStop" @click="stop">
        套中前方
      </button>
      <button
        class="common-button primary"
        :disabled="!canStop"
        @click="stop(-1, true)"
      >
        套中前方并逃跑
      </button>
      <button
        v-for="(item, index) in prizeImgArray"
        :key="index"
        class="common-button primary"
        :disabled="!canStop"
        @click="stop(index)"
      >
        套中奖品{{ index }}
      </button>
      <button
        v-for="(item, index) in prizeImgArray"
        :key="index"
        class="common-button primary"
        :disabled="!canStop"
        @click="stop(index, true)"
      >
        套中奖品{{ index }}并逃跑
      </button>
    </demo-block>
  </div>
</template>
<script>
import { StateConstant } from '@tuia/market-ui';

export default {
  data() {
    return {
      state: 0,
      prizeImgArray: [
        '//yun.tuisnake.com/mami-media/img/50d7608a-ociowk8pm5.png',
        '//yun.tuisnake.com/mami-media/img/691aa62e-d5xwaga1hn.png',
        '//yun.tuisnake.com/h5-mami/couponPrize/lucky.png',
      ],
    };
  },

  computed: {
    canStart() {
      return this.state === StateConstant.WAIT_START;
    },
    canStop() {
      return this.state === StateConstant.WAIT_END;
    },
  },

  methods: {
    stateChange(opt) {
      this.state = opt.now;
    },

    async clickStart() {
      const ringtoss = this.$refs.ringtoss;
      if (!ringtoss.isWaitStart) {
        return;
      }
      await ringtoss.start();
    },

    async stop(index = -1, escape = false) {
      const ringtoss = this.$refs.ringtoss;
      let catchIndex = ringtoss.getHitIndex();
      let waitCheck = false;
      if (index > -1 && index !== catchIndex) {
        catchIndex = index;
        waitCheck = true;
      }
      await ringtoss.stop({ index: catchIndex, escape, waitCheck });
    },

    async reset() {
      await this.$refs.ringtoss.reset();
    },

    disable() {
      this.$refs.ringtoss.disable();
    },
  },
};
</script>
